
body{
  padding: 0;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
}
#app-body{
  background: -webkit-gradient(linear, left top, right bottom, from(rgb(254, 255, 255)) , to(rgb(137, 212, 255)));
  background: linear-gradient(to bottom right, rgb(254, 255, 255) , rgb(137, 212, 255));
}
@media only screen and (min-width: 700px){
#app{
    display: grid;
    grid-template-columns: 20% 4fr;
    max-width: 100vw;
    margin: 0 auto;
    grid-template-areas: "app-nav app-body"
}
#app-nav{
    grid-area: 'app-nav';
    width: 100%;
    height: 100vh;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-right: 2px solid rgba(0, 89, 255, 0.13);
}
#app-body{
    grid-area: "app-body";
}
}
@media only screen and (max-width: 700px){
