      padding-left: 10px;
      float: left;
}
#nav-small-sign[data-v-109628ef]{
      float: right;
}

    /* Menu icon copied */
.menu-icon[data-v-109628ef] {
      position: relative;
      top: 0.5em;
      display: block;
      width: 4em;
      height: 2.5em;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.menu-icon > span[data-v-109628ef] {
      position: absolute;
      top: 50%;
      display: block;
      width: 80%;
      height: 0.5em;
      margin-top: -0.5em;
      background-color: #358e9e;
      border-radius: 3px;
}
.menu-icon > span[data-v-109628ef]:before, .menu-icon > span[data-v-109628ef]:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: #358e9e;
      border-radius: 3px;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.menu-icon > span[data-v-109628ef]:before {
      -webkit-transform: translateY(-1em);
      transform: translateY(-1em);
}
.menu-icon > span[data-v-109628ef]:after {
      -webkit-transform: translateY(1em);
      transform: translateY(1em);
}

    /* 
     OPENED
    */
.menu-icon.open[data-v-109628ef] {
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
}
.menu-icon.open > span[data-v-109628ef]:before {
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg);
}
.menu-icon.open > span[data-v-109628ef]:after {
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg);
}
}

